module.exports = [{"title":"What is Game Theory?","meta":{"date":"15/02/2018","description":"Drivers manoeuvring in heavy traffic are playing a driving game. Bargain-hunters bidding on eBay are playing an auctioning game. A firm and a union negotiating next years wage are playing a bargaining game. When opposing candidates choose their platform in an election, they are playing a political game. The owner of a grocery store deciding todays price for corn flakes is playing an economic game. In brief, a game is being played whenever human beings interact."},"markdown":"# What is Game Theory?\n\nIn the last post, I examined the concept of play, in this post I want to get closer to understanding why we might play a game by examining the choices players are afforded by the constraints of a given set of rules. I am looking purely at the mechanic's of gameplay as for me it is the mental exercise of attempting to preempt my opponent's decisions that make a game experience enjoyable. An example of a game that does this well is chess, there is no element of chance and the level of complexity means there is no obvious best move. Tic Tac Toe on the other hand again doesn't involve chance but the number of solutions is so small that it is relatively easy to see what the best possible move is and from then on every game becomes a tie. The key here is complexity, not so much that it is hard to learn and remember the rules for but enough that for the majority of the game the best possible choice of move cannot be worked out. This is a personal preference but one that I suspect is crucial to helping games be replayable. A relatively new area of study has developed around analysing the choices of players in games called Game Theory and I use this post to give a short introduction.\n\nGame Theory was developed by Von Neuman and Oskar Morgenstern and then pioneered by John Nash who was made famous in the movie 'A Beautiful Mind' (Ross, 2016). Its applications are widespread as a game of sorts is played every time we interact with other people, \"Drivers manoeuvring in heavy traffic are playing a driving game. Bargain-hunters bidding on eBay is playing an auctioning game.\" (Binmore 2008, p.1). A formal definition for a game that could be studied in game theory is:\n\n> The interaction among rational, mutually aware players, where the decisions of some players impacts the payoffs of others. A game is described by its players, each player's strategies, and the resulting payoffs from each outcome. \n>\n> (Shor, 2018)\n\nThe definition of rationality in game theory is borrowed from economics and is in this case that players in a game assess all outcomes and make their decisions based solely of maximising their 'utility' (chance of winning) (Ross, 2016). Utility here is another term from economics which is an abstract numerical value assigned to all the possible outcomes of a game for a player (Binmore, 2008).\n\nAlthough Game Theory isn't directly helpful in the actual creative production of games it doesn't for instance measure how much we enjoy a game or what aspects of a game we enjoy but it does break games down by using tools like decision trees. What attracted me initially is the thought experiments such as the 'Prisoners Dilemma' which illustrate interesting challenges for the players involved with relatively few rules. In my next post, I want to look at some of the thought experiments and how games are used in Behaviour science.\n\n### References\n\n- Ross, D. (2016). *Game Theory*. [online] The Stanford Encyclopedia of Philosophy. Available at: https://plato.stanford.edu/archives/win2016/entries/game-theory/ [Accessed 15 Feb. 2018].\n- Binmore, K. (2008). *Game theory*. Oxford: Oxford University Press.\n- Shor, M. (2018). Game - Game Theory .net. [online] Gametheory.net. Available at: http://www.gametheory.net/dictionary/Game.html [Accessed 15 Feb. 2018].\n\n\n\n"},{"title":"Play in Language","meta":{"date":"07/02/2018","description":"A general notion of play"},"markdown":"# Play in Language\n\nThe study of play as a concept is large and has an extensive body of literature behind it, so as a way to stay focused I am going to use this post to look at the definitions of the word itself. I look primarily at chapter two of Johan Huizinga's book 'Homo Ludens' one of the earliest comprehensive studies on 'Play'. This chapter focuses on how languages differ widely in what they categorize as play and how cultures have grouped play like activities differently.\n\nA general abstract notion of play doesn't exist in many languages and developed late in many others, even within the Indo-European languages, there is no general notion of play indicating that despite its fundamental function it has as a concept has been secondary (Huizinga, 1980). This ambiguity in the play concept is apparent in the Oxford English Dictionary where there are 7 entries for its use as a verb alone, I am going to just look at the first two, the first being, to \"Engage in activity for enjoyment and recreation rather than a serious or practical purpose.\" and the second being to \"Take part in (a sport)\" (Oxford, 2018). A second definition is needed because there is a serious aspect to sport and despite it being mostly done for enjoyment and recreation there are those who compete professionaly, and I assume treat it more like work than play. In Greece where competitive sport is a large part of culture there is a separate word used to describe the participation in competition one destinct from the words that define the other play activities, this is simmilar in sanskrit. Nevertheless, if we are to define a general notion of play, Huizinga argues that competition \"bears all the formal characteristics of play, and as to its function belongs almost wholly to the sphere of the festival, which is the play sphere\" (Huizinga, 1980, p.31).\n\nIn English we refer to the socialising of small children as playing but there is a point (In my case early teens) where we substitute the word play for 'Hang out', 'Meet up', 'Chill' or just the name of an activity such as bowling or watching a movie. After talking to some chinese friends I discovered that this is not the case in Chinese where it is fitting at any age to use the word wan (play) to describe activities with friends or even your own hobbies. This points to a bias in the west to see play as something more childish something that serius adults don't do and yet the 'Informal Social Play' (Sutton-Smith, 1998, p.4) that adults participate in seems to me not dissimalar to the social games of children.\n\nPlay is intrinsic to interaction design, not only in creative production but to the engagement of audiences and so a deeper understanding of the nature of play seems crucial to a designer. Here, I have only touched on two small aspects of play the contention between competitive and non competive play and the ambiguities between child and adult play. It is as I said at the beggining a large subject and I plan in future posts to explore further into the concept of play.\n\n#### References\n\n- Huizinga, J. (1980). *Homo ludens*. London: Routledge and Kegan Paul. (Huizinga, 1980)\n- Oxford Dictionaries | English. (2018). *play | Definition of play in English by Oxford Dictionaries*. [online] Available at: https://en.oxforddictionaries.com/definition/play [Accessed 7 Feb. 2018].\n- Sutton-Smith, B. (1998). *The ambiguity of play*. Cambridge, Mass.: Harvard University \n\n\n\n\n"},{"title":"What is Conceptual Art?","meta":{"date":"30/11/2017","description":"'In conceptual art, the idea or concept is the most important aspect of the work. When an artist uses a conceptual form of art, it means that all of the planning and decisions are made beforehand and the execution is a perfunctory affair.' (Sol LeWitt)"},"markdown":"# What is Conceptual Art?\n\n## Introduction\n\nContemporary art can be difficult to access and contrary to its origins can feel restricted to art historians or the experts running the galleries. I certainly have felt this way especially in regards to the more avant-garde practices of modern artists and I want in this essay to give a clear definition of Conceptual art in regards to the movement which took place in the late 1960's early 1970's. It was a movement that started to question the very nature of art through a diverse range of practices where the emphasis was on the concept or idea behind the work. Much of the movement was focused on the use of language as an artistic medium and it is unique in that it is one of the last great leaps in artistic thought and perhaps the most important of the movements to have at least a rough understanding of, as some of the pieces can seem on the surface bizarre and nonsensical without context.\n\nIt can be hard to talk about definition and meaning in art as many critics opinions differ, it is also difficult to give enough context without going down a rabbit hole of different movements, idea and thoughts. I have in this essay chosen to start with Marcel Duchamp and his piece 'Fountain' as I think it is a relatively simple example of a Conceptual work. I then jump forward to the 1960's and 1970's because this is where a more discernable movement is seen. To underlay some context I briefly describe art prior to the conceptual art movement specifically the group called 'Fluxus' and the Minimalism movement as they were key to the birth of Conceptual art (Godfrey, 2011).\n\n## Fountain\n\n'Fountain' was first submitted to the inaugural exhibition of the newly formed Society of Independent Artists in 1917 in New York. The society was formed by a group of artists who wanted a more democratic and open place for artists to exhibit work, in their first public notice they wrote about a great need for a place where \"artists of all schools can exhibit together certain that whatever they send will be hung\" (Camfield and Duchamp, 1989, p.66). 'Fountain' was submitted under the pseudonym R, Mutt,  under the instructions of Marcel Duchamp who was head of the hanging committee for the exhibition. It is likely he submitted the piece to test the societies commitments to their rules, and chose to use the pseudonym so as not to let his name influence his colleague's opinions. After heated debates lasting until the morning of the exhibition, it was decided by majority vote not to exhibit the piece. The decision caused some controversy in the media and Duchamp and one of the other members resigned the society in protest. Some of the members felt it was \"Vulgar\" others worried it was a joke possibly further confused as the name \"Mutt\" was the same name as a character in a popular newspaper cartoon, it was also difficult to see an object which the artist didn't actually make just simply signed, as art and understandably so I think even now many people struggle to see the value of calling something like 'Fountain' art. As an object, it is just a urinal placed in a gallery instead of the bathroom where it is normally seen. (Tate, Fountain, 2017) (Camfield and Duchamp, 1989)\n\n'Fountain' is part of an oeuvre of work Duchamp labelled as 'readymades', which although he never endorsed a clear definition historians have supported Andre Breton's definition, \"manufactured objects promoted to the dignity of art through the choice of the artist\" (Girst, 2014, p.154). The idea is that any object can be called art if an artist says it is. Duchamp explains this in the second issue of The Blind Man, a magazine he co-founded with Henri-Pierre Roche and Beatrice Wood, which he published soon after the exhibition. \"Whether Mr Mutt with his own hands made the fountain or not has no importance. He Chose it. He took an ordinary article of life, placed it so that its useful significance disappeared under the new title and point of view created a new thought for that object\" (Duchamp, Roche and Wood, 1917, p.5). He is saying that the creative act is in the choosing of the object and that by stripping away its utility value it becomes art and finally by displaying it in a gallery by changing the context in which it is seen it invokes a new thought (Tate, Readymade, 2017).\n\nThe new thought that 'Fountain' creates is in the form of a question asking what can and cant be art. It is not merely a urinal in a gallery but a proposition for a wider a definition. That proposition is not the urinal itself but the idea that an idea can be art. For this reason, 'Fountain' is sometimes referenced as the first conceptual artwork, It is the fact that the thought around the piece is more important than the physical object and that it is a piece of art criticising art, that it is self-referential. Calvin Tomkins and art critic who interviewed Duchamp on a number of occasions describes this in an interview, \"The real point of the readymades was to deny the possibility of defining art. Art can be anything. It isn't an object or even an image, its an activity of the spirit\" (Tomkins, 2013, p.17).\n\n## History\n\nThe fifties was a time of healing in the West, of slow regeneration, of rebuilding after the destruction of the second world war, it was above all a time of hope. Abstract expressionism was dominant and reflected the attitude of the time with its brightly coloured abstract paintings. But brewing underneath this apparent contentment was the need for social reform this showed itself in the black rights movements, the youth groups campaigning for social reforms, who in the 1960's would be labelled as the hippies. While the American government was distracted battling communism in Vietnam a new spirit was rising \"a new wind of essentially youthful hostility to every kind of established convention and traditional authority, a wind of moral freedom and rebellion\" (Booker, 1992, p.26). The 1960's brought cultural reformation, people began to challenge old traditions fighting not only for the rights of people but questioned the taboos of sex and the prohibition of drugs. So too in art, there was a rising discontentment with the commercial galleries and a malaise over traditional forms, this could be seen in the dada-like practices of the Fluxus and the complete disregard of renaissance values shown within minimalism. It was the work produced by the Fluxus group and the Minimalist movement which gave rise to Conceptual art. (Godfrey, 2011)\n\nThe Fluxus was an international group of artists founded by  Lithuanian/American artist George Maciunas who wrote in their manifesto that the purpose of the group was to \"Promote a revolutionary flood and tide in art. Promote living art, anti-art, promote non-art reality to be grasped by all peoples\" (Phillpot, 2017). They were anti-commercialism avoided academics instead opting for a more democratic philosophy opening creativity to anyone. As a group they played a crucial role in further opening questions about the nature of what art could be which would later become the dominant discourse in the conceptual works. Minimalism, on the other hand, was interested in viewership they were interested in art that didn't go beyond the object/thing itself, it didn't represent an emotion or idea it was simply the thing. Mel Bochner wrote in an exhibition review that what the artists held in common \"is the attitude that Art - from the root artificial - is unreal, constructed, invented,  make-believe\" (Godfrey, 2011, p.111). The art was often simple and geometric \"the works were so 'ordinary' that the viewer was made to think about the uncertain distinction between things in art and things in the world\" (Godfrey, 2011, p.112). It is this self-awareness and engagement with the viewer on a level beyond the emotional which carried over into the Conceptual pieces. (Godfrey, 2011)\n\n## Conceptual art\n\nLike the Fluxus, the conceptual works that emerged in the late 1960's shunned the commercial side of art valuing thought process over art objects making it difficult to sell the works in galleries (Tate, Conceptual art, 2017). Where possible they tried to avoid letting in arbitrariness they were problem solvers they wanted to invoke\n\nthought alone and so the choice of form was restricted to the unaesthetic often to two dimensions. In many ways, the artists acted more as curators like a writer choosing words the forms acted as the \"grammar for the total work.\" (LeWitt, 1967). Duchamp too actively avoided making decisions based on aesthetics \"the choice of the readymades never was a result of aesthetic delectation.\" (Tomkins, 2013, p.54).\n\nSol Lewitt writes about the importance of the entire process for a conceptual work: \"All intervening steps scribbles, sketches, drawings, failed works, models, studies, thoughts, conversations are of interest\" (LeWitt, 1967). This is perhaps best illustrated with Mel Bochner's piece 'Working Drawings and Other Visible Things on Paper Not Necessarily Meant to Be Viewed as Art' presented at the School of Visual Arts in 1966. For the piece Bochner collected drawings, sketches and documents from his friends who included Sol LeWitt, Dan Flavin, John Cage, Donald Judd, among others and as the exhibition would not pay to have them framed he photocopied them to a standard size and presented them in loose-leaf binders on pedestals (Frieze.com, 2017). Minimalism \"emphasizes an obscuring, even an erasure, of the artists hand\", Bochner's piece, on the other hand, served to reintroduce the hand into its process and \"these drawings expose the process of creation and stand as vital counterpoints to the sterile perfection\" of minimalism (Malone, 2012).\n\nConceptual art questions the traditional norms of art objects as unique by posing questions in the form of artworks. One of the earliest definitions was by Solomon Lewit who wrote in his essay 'Paragraphs on Conceptual Art':  \"In conceptual art, the idea or concept is the most important aspect of the work. When an artist uses a conceptual form of art, it means that all of the planning and decisions are made beforehand and the execution is a perfunctory affair.\" (LeWitt, 1967).\n\n## Conclusion\n\nThere was a rapid evolution in thought throughout the 20th century, perhaps the most important of these was the ability to be self-critical. Culturally this meant we could escape the dogmas of religion into a philosophy of autonomy, in science, it meant defining clearly what we don't know and in art it allowed us to widen the strictures so far that ideas can be viewed as art. This was an important shift in thinking as it opens up the institution of art to criticism from the inside and expands the role of artists beyond the visceral to the cerebral. It is also interesting to note that this trend in the 1960's to dismantle formalism was championed by one of the first generations of artists who had been educated in university and literature around their work was often dense and at times hermetic (Godfrey, 2011). It seems to me this is a pattern that often occurs with new ideas, where the initial literature is arcane and filled with jargon and it takes future generations to tease apart meaning and restructure so as to make the ideas accessible.\n\nThe rules described above for what is and isnt Conceptual art refer to the movement in the late 1960's and are based on Sol Lewitt's 1966 essay 'Paragraphs on Conceptual art'. They are not necessarily rules to follow if you want to create work that invokes thought, I can't see the perceptual experience of aesthetic beauty and the intellectual experience of expanded thinking as mutually exclusive. It is a danger though that the conceptual part of an artwork might be overlooked or ignored if aesthetically pleasing.\n\n#### References\n\nBooker, C. (1992). The neophiliacs. London: Pimlico.\n\nDuchamp, M., Roche, H. and Wood, B. (1917). The Richard Mutt Case. The Blind Man, (02), p.5.\n\nFoster, H., Krauss, R., Bois, Y. and Buchloh, B. (2007). Art since 1900. 1st ed. New York, N.Y.: Thames & Hudson, pp.527-533.\n\nFrieze.com. (2017). Mel Bochner. [online] Available at:[ ](https://www.google.com/url?q=https://frieze.com/article/mel-bochner&sa=D&ust=1517591386139000&usg=AFQjCNGCGNzFaPnVMECdNScptC00zzyuCw)[https://frieze.com/article/mel-bochner](https://www.google.com/url?q=https://frieze.com/article/mel-bochner&sa=D&ust=1517591386139000&usg=AFQjCNGCGNzFaPnVMECdNScptC00zzyuCw) [Accessed 5 Dec. 2017].\n\nGirst, T. (2014). The Duchamp dictionary. London: Thames & Hudson.\n\nGodfrey, T. (2011). Conceptual art. London: Phaidon.\n\nGreenberg, C. and O'Brian, J. (2007). The collected essays and criticism. Chicago [u.a.]: Univ. of Chicago Pr.\n\nLeWitt, S. (1967). Paragraphs on Conceptual Art. Artforum, 5(10), pp.79-83.\n\nMalone, M. (2012). The Porous Practive of Drawing: System, Seriality, and the\n\nHandmade Mark  in Minimal and Conceptual Art. [online] Available at:[ ](https://www.google.com/url?q=http://notations.aboutdrawing.org/essay/&sa=D&ust=1517591386141000&usg=AFQjCNGck112WeNt-uERbQIhHRf3pYkT8g)[http://notations.aboutdrawing.org/essay/](https://www.google.com/url?q=http://notations.aboutdrawing.org/essay/&sa=D&ust=1517591386141000&usg=AFQjCNGck112WeNt-uERbQIhHRf3pYkT8g)\n\nNews.bbc.co.uk. (2017). BBC NEWS | Entertainment | Arts | Duchamp's urinal tops art survey. [online] Available at:[ ](https://www.google.com/url?q=http://news.bbc.co.uk/1/hi/entertainment/4059997.stm&sa=D&ust=1517591386142000&usg=AFQjCNGSvOIXOpPpbvbTcJ6m5TYWyWJSwA)[http://news.bbc.co.uk/1/hi/entertainment/4059997.stm](https://www.google.com/url?q=http://news.bbc.co.uk/1/hi/entertainment/4059997.stm&sa=D&ust=1517591386142000&usg=AFQjCNGSvOIXOpPpbvbTcJ6m5TYWyWJSwA) [Accessed 25 Nov. 2017].\n\nNorton, L. (2017). Buddha of the Bathroom. The Blind Man, [online] (02). Available at:[ ](https://www.google.com/url?q=http://sdrc.lib.uiowa.edu/dada/blindman/2/05.htm&sa=D&ust=1517591386143000&usg=AFQjCNEW30mJXGK2ZQulJW4dY7gQh4ds3A)[http://sdrc.lib.uiowa.edu/dada/blindman/2/05.htm](https://www.google.com/url?q=http://sdrc.lib.uiowa.edu/dada/blindman/2/05.htm&sa=D&ust=1517591386143000&usg=AFQjCNEW30mJXGK2ZQulJW4dY7gQh4ds3A) [Accessed 27 Nov. 2017].\n\nPhillpot, C. (2017). Manifesto I. [online] George Maciunas Foundation Inc. Available at:[ ](https://www.google.com/url?q=http://georgemaciunas.com/about/cv/manifesto-i/&sa=D&ust=1517591386143000&usg=AFQjCNHuGVVMgiGwvoehzLNAIhQZwYYR5Q)[http://georgemaciunas.com/about/cv/manifesto-i/](https://www.google.com/url?q=http://georgemaciunas.com/about/cv/manifesto-i/&sa=D&ust=1517591386144000&usg=AFQjCNEpeVbQf1OURY_ZdiC3mtdFdqbMHA) [Accessed 4 Dec. 2017].\n\nTate. (2017). Conceptual art  Art Term | Tate. [online] Available at:[ ](https://www.google.com/url?q=http://www.tate.org.uk/art/art-terms/c/conceptual-art&sa=D&ust=1517591386144000&usg=AFQjCNGlo8Lg4YHWPqovz7BF4mVMU_43Ew)[http://www.tate.org.uk/art/art-terms/c/conceptual-art](https://www.google.com/url?q=http://www.tate.org.uk/art/art-terms/c/conceptual-art&sa=D&ust=1517591386144000&usg=AFQjCNGlo8Lg4YHWPqovz7BF4mVMU_43Ew) [Accessed 27 Nov. 2017].\n\nTate. (2017). Fountain, Marcel Duchamp, 1917, replica 1964 | Tate. [online] Available at:[ ](https://www.google.com/url?q=http://www.tate.org.uk/art/artworks/duchamp-fountain-t07573&sa=D&ust=1517591386145000&usg=AFQjCNFC2IrewZFbdripq5_5dN-ifyFeBw)[http://www.tate.org.uk/art/artworks/duchamp-fountain-t07573](https://www.google.com/url?q=http://www.tate.org.uk/art/artworks/duchamp-fountain-t07573&sa=D&ust=1517591386145000&usg=AFQjCNFC2IrewZFbdripq5_5dN-ifyFeBw) [Accessed 28 Nov. 2017].\n\nTate. (2017). Readymade  Art Term | Tate. [online] Available at:[ ](https://www.google.com/url?q=http://www.tate.org.uk/art/art-terms/r/readymade&sa=D&ust=1517591386146000&usg=AFQjCNFAV75kw5XzVVK8mTAtsZRqWPwDAg)[http://www.tate.org.uk/art/art-terms/r/readymade](https://www.google.com/url?q=http://www.tate.org.uk/art/art-terms/r/readymade&sa=D&ust=1517591386146000&usg=AFQjCNFAV75kw5XzVVK8mTAtsZRqWPwDAg) [Accessed 26 Nov. 2017].\n\nTomkins, C. (2013). Marcel Duchamp: The Afternoon Interviews. Badlands Unlimited.\n\n\n\n"},{"title":"Death of the Author","meta":{"date":"25/11/2017","images":{"thumbnail":{"src":"https://upload.wikimedia.org/wikipedia/en/7/7c/RolandBarthes.jpg"}},"description":"It is as though we are each a neuron in the large brain network we call humanity and our job as this neuron is simply to take in information from other neurons and spit out the sum of that information."},"markdown":"# Death of the Author\n\nIt seems intuitive when critiquing literature to ask what the author is trying to say, but Roland Barthes in his essay 'Death of the Author' published in 1960 offers an alternative idea, one that I now see as far more pragmatic. I have always been fond of deterministic philosophies, as they explain why it is that I do not know why I might choose one thing over another. I also see it as the pragmatic philosophy when it comes to fixing problems within institutions, in the aerospace industry for instance they are famous for giving pilots full immunity when they admit to any mistakes allowing them to redesign the plains so as to protect from future pilots making the same mistake. It is a culture of blaming the environment not people (Syed, 2016). In creative production, I too tend to believe in a sort of determinism and Barthes ideas seem a natural conclusion from such a belief system. I have written the essay from this perspective, however, a belief in free will does not invalidate Barthes ideas around authorship and spectatorship.\n\nBarthes argues in the essay that it makes more sense to critique writing as a thing in itself, not as an expression of the author, hence 'death of the author'. Essentially a piece of writing is not something that comes from the author alone it comes from what the author has read and experienced, it comes from the environment, \"a text consists of multiple writings, issuing from several cultures and entering into dialogue with each other\" (Barthes, 1976). Barthes also points out that it is essentially pointless as a critic to even factor in the author as it is not the author who will imbue meaning but the reader. Whether the meaning that the author intended is the same as the meaning that is interpreted by the reader doesn't matter, what matters is just the meaning interpreted by the reader, \"the reader is the very space in which are inscribed, without any being lost, all the citations a writing consists of\" (Barthes, 1967).\n\nBarthes talks about authorship and spectatorship in respect to writing and literature but the ideas can be transferred to any sort of creative production. What I find particularly attractive about this idea is that it shifts focus away from the worship of individuals towards an admiration for the subject more generally. I think this perspective could also have profound impacts on the way we practice, if, for instance, we no longer see our creations as our own but as belonging to the world, the response to our work will not affect our motivation to practice. It is as though we are each a neuron in the large brain network we call humanity and our job as this neuron is simply to take in information from other neurons and spit out the sum of that information. The analogy may be ugly in the way portrays us as simple machines but I think it also humbling in that it gives back our creations to the world and humanity in general.\n\n#### References\n\nBarthes, R. (1967). The Death of the Author. [online] Ubuweb. Available at:[ ](https://www.google.com/url?q=http://www.ubu.com/aspen/aspen5and6/threeEssays.html%23barthes&sa=D&ust=1517591482673000&usg=AFQjCNGkMmrejnho8tMwAfie3fB0Q6J4wg)[http://www.ubu.com/aspen/aspen5and6/threeEssays.html#barthes](https://www.google.com/url?q=http://www.ubu.com/aspen/aspen5and6/threeEssays.html%23barthes&sa=D&ust=1517591482673000&usg=AFQjCNGkMmrejnho8tMwAfie3fB0Q6J4wg)\n\nSyed, M. (2016). Black box thinking. Portfolio Penguin.\n\n\n\n"},{"title":"Who are the Oulipo?","meta":{"date":"12/11/2017","description":"'Rats who must build the labyrinth from which they propose to escape.' (Warren Motte)"},"markdown":"# Who are the Oulipo?\n\nCent Mille Milliards de Pomes (Hundred Thousand Billion Poems) was a book published in 1961, made up of 14 ordered groups of 10 line poems, the idea was that a reader picks one line from each of group to build a 14 line sonnet. Because of the way combinations work in mathematics the books essentially contained 10^14 possible poem variations. During the writing of the book, Raymond Queneau enlisted the help of mathematician Franois Le Lionnais and it was this collaboration which sparked the creation of the Oulipo in 1960 (Gallix, 2013). Ouvroir de littrature potentielle (workshop of potential literature) or Oulipo is a group for people interested in, as Daniel Levin Becker a more recent member of the group put it, \"investigations of poetic form and narrative constraint and the mathematics of wordplay\" (Levin Becker, 2012). It is rare such disparate topics, poetry and mathematics, are seen in the same sentence and so I use this essay to explore a little further into the Oulipo and this relationship between creative writing and mathematics.\n\nThe Oulipo is made up of an eclectic assortment of individuals including writers, poets, translators, graph theorists, lexicographers, actors, historians, diplomats, etcetera  (Levin Becker, 2012). One of the earliest definitions proposed, to describe the assorted members was, \"rats who must build the labyrinth from which they propose to escape\" (Motte, 1986, p.25). Becker describes this in more detail in his book \"Many subtle channels\", \"writers are constrained whether or not they acknowledge it-not just by the strictures of poetic forms like the sonnet or the haiku, but also by the conventions of their chosen genre, the format in which they publish, even the grammar and lexicon of their native (or adopted) language. Embracing a set of carefully chosen rules is meant to focus the mind so narrowly that those obscure pressures and preoccupations fade, revealing paths and passageways that one would never notice without the blinders.\" (Levin Becker, 2012). This is an idea that I can see working in all forms of creative production, it is a way of avoiding getting so-called \"writer's block\", instead, switching the mind from arbitrary idea generation to problem-solving.\n\nI find this idea deeply attractive; through learning software and programming paradigms, I have increasingly come to see humans as complex machines and creativity the operation of making connections between existing ideas. If this is the case it makes sense for us systematize the creative process in order to best take advantage of our brains abilities, which at least in my own case is problem-solving. Interestingly in 1960 around the same time the Oulipo formulated, a psychologist called Sarnoff Mednick first proposed this idea that, \"creativity is associative memory that works exceptionally well\" (Kahneman, 2015, p.67). I don't want to generalise when I say it makes sense to \"systematize the creative process\" it is a subject which deserves far more research but it is a technique that works for this unique group of academics and creatives which has lasted now nearly 60 years.\n\n#### References\n\nGallix, A. (2013). Oulipo: freeing literature by tightening its rules. [online] the Guardian. Available at: [https://www.theguardian.com/books/booksblog/2013/jul/12/oulipo-freeing-literature-tightening-rules](https://www.theguardian.com/books/booksblog/2013/jul/12/oulipo-freeing-literature-tightening-rules) [Accessed 9 Nov. 2017].\n\nKahneman, D. (2015). *Thinking, fast and slow*. New York: Farrar, Straus and Giroux, p.67.\n\nLevin Becker, D. (2012). *Many subtle channels*. Cambridge, Mass.: Harvard University Press, pp.6,10.\n\nMotte, W. (1986). *Oulipo*. Normal, Ill.: Dalkey Archive Press, p.25.\n\nMotte, W. (2006). Raymond Queneau and the Early Oulipo. *French Forum*, [online] 31(1), pp.41-54. Available at: [https://arts.idm.oclc.org/login?url=https://search-proquest-com.arts.idm.oclc.org/docview/198771606?accountid=10342](https://arts.idm.oclc.org/login?url=https://search-proquest-com.arts.idm.oclc.org/docview/198771606?accountid=10342).\n\nOulipo.net. (2017). Oulipo | Oulipo. [online] Available at: [http://oulipo.net/fr/oulipiens/o](http://oulipo.net/fr/oulipiens/o )[Accessed 12 Nov. 2017].\n\n\n\n\n\n"},{"title":"Kenneth Goldsmith","meta":{"date":"15/10/2017","images":{"thumbnail":{"src":"https://i.amz.mshcdn.com/vjMb4JA2vT9aGMWiE1JJHCcehwI=/950x534/filters:quality(90)/https%3A%2F%2Fblueprint-api-production.s3.amazonaws.com%2Fuploads%2Fcard%2Fimage%2F188327%2FGettyImages-174509399.jpg"}},"description":"A man who spent a year transcribing a copy of the New York Times"},"markdown":"# Kenneth Goldsmith\n\nGoldsmith is seen by most as a conceptual poet but his practice extends into curation, sound, and art criticism. he is by most accounts a provocateur, he appreciates pranks, in an interview, he expresses his love for artist Marcel Duchamp and comedian Sacha Baron Cohen, who are famous for taking ideas to extremes in art and comedy respectfully (Louisiana, 2014). Perhaps when Goldsmith published his most famous book \"Day\", a direct transcription of a copy of the New York Times, he is, by calling it poetry mirroring what Marcel Duchamp did with \"Fountain\"; he is questioning what poetry could be and like Cohen did with his character Borat he is taking this idea to an extreme. Goldsmith calls this practice of appropriation and transcription \"uncreative writing\" and I have struggled to understand the value of such a bizarre practice and so I use this essay to explore why Goldsmith dedicates so much time to something which seems to me so binal.\n\nWatching interviews I was quickly seduced by Goldsmiths charm, he wears small round eyeglasses nestled in a large and bushy beard, he talks with a meticulous and unambiguous clarity taking time to pause between sentences. It helps that he is aware of the impression his work can have on people, he is quoted a New Yorker article saying \"Im the most boring writer who has ever lived\" (Wilkinson, 2017), in fact, he worries that if too many people did buy his books he might be sued for copyright infringement (Louisiana, 2014). Like with conceptual art, his work is less about the material he produces than it is about the thought behind his work, he says I dont have a readership, I have a thinkership (Wilkinson, 2017). Goldsmith is well renowned within the world of avant-garde poetry, he was the first New York Museum of Modern Art's poet laureate and even read his poetry to President Obama at the white house (Wilkinson, 2017), but even so there are those that criticise him, Cathy Park King, for instance, described his work as irrelevant considering all of the societal and political unrest and accused him of jumping on the racial bandwagon by reading browns autopsy at brown university (Hong, 2015).\n\nDespite understanding that \"Day\" was a conceptual piece and so about provoking thought I found myself quite unthoughtful instead in more of a state of vexed confusion and it took hearing him talk for me to start to think about what this practice of uncreative writing might mean in a broader context. It was the idea that curation and appropriation can be an artistic practice that I found particularly captivating. There are according to software engineer Leonid Taycher 129,864,880 books in the entire world, this is such a large number that no one person could come close to reading even a fraction of them and this doesn't include films and music (Taycher, 2010). It seems to me that as an artist you are just as valuable to society if you bring to public attention pertinent old ideas as you would be attempting to create something new and original. Goldsmith to me points out that like how the Dtournement movement gave new meaning to slogans by changing their context, the same can be done with writing.\n\n#### References\n\nTaycher, L. (2010). Books of the world, stand up and be counted! All 129,864,880 of you.. [online] Available at: [http://booksearch.blogspot.co.uk/2010/08/books-of-world-stand-up-and-be-counted.html](http://booksearch.blogspot.co.uk/2010/08/books-of-world-stand-up-and-be-counted.html)[Accessed 7 Nov. 2017].\n\nFrieze (2014). No Such Thing as Writer's Block. [video] Available at: [https://www.youtube.com/watch?v=9Box0FF9H0Q](https://www.youtube.com/watch?v=9Box0FF9H0Q) [Accessed 15 Oct. 2017].\n\nHong, C. (2015). There's a New Movement in American Poetry and It's Not Kenneth Goldsmith. [online] New Republic. Available at: [https://newrepublic.com/article/122985/new-movement-american-poetry-not-kenneth-goldsmith](https://newrepublic.com/article/122985/new-movement-american-poetry-not-kenneth-goldsmith)[Accessed 7 Nov. 2017].\n\nLouisiana Museum of Modern Art (2014). Assume No Readership. [video] Available at: [https://www.youtube.com/watch?v=FAJRQJGc7DU](https://www.youtube.com/watch?v=FAJRQJGc7DU) [Accessed 15 Oct. 2017].\n\nWilkinson, A. (2017). Something Borrowed. New Yorker. [online] Available at: [https://www.newyorker.com/magazine/2015/10/05/something-borrowed-wilkinson ](https://www.newyorker.com/magazine/2015/10/05/something-borrowed-wilkinson)[Accessed 15 Oct. 2017].\n\nWriting.upenn.edu. (2017). [online] Available at: [http://www.writing.upenn.edu/~afilreis/88v/goldsmith-bio.html](http://www.writing.upenn.edu/~afilreis/88v/goldsmith-bio.html)[Accessed 6 Nov. 2017].\n\n\n\n\n\n"},{"title":"The social effect of the 1935-6 exhibition of Chinese art at Burlington House, London","meta":{"date":"25/05/2017","cover":true,"images":{"gallery":["projects/burlington/images/ Gallery IV.jpg","projects/burlington/images/ The crew of H.M.S. Hood unload cases at Portsmouth, July 1935.jpg","projects/burlington/images/ Zheng Tianxin (Dr F. T. Cheng) and Mr F.St. G. Spendlove examining exhibits.jpg","projects/burlington/images/A Knife-grinder.jpg","projects/burlington/images/A Manchu Bride.jpg","projects/burlington/images/A Mandarin official.jpg","projects/burlington/images/A Muslim Butcher.jpg","projects/burlington/images/A Travelling Chiropodist.jpg","projects/burlington/images/Amoy man and woman.jpg","projects/burlington/images/Boatman, Canton.jpg","projects/burlington/images/Buddhist Sculpture in The Lecture Room.jpg","projects/burlington/images/Early Ming Dynasty, Gallery VIII (Royal Academy of Arts, The International Exhibition of Chinese Art, 1935-36), 1935.jpg","projects/burlington/images/Gallery 7.jpg","projects/burlington/images/Gallery I  03.jpg","projects/burlington/images/Gallery I 02.jpg","projects/burlington/images/Gallery I.jpg","projects/burlington/images/Gallery II.jpg","projects/burlington/images/Hoisting a case from the deck of H.M.S. Suffolk at Portsmouth, July 1935.jpg","projects/burlington/images/Installing the Amitabha Buddha in the Central Hall. .jpg","projects/burlington/images/MISSION SCHOOL, AMOY, CHINA.jpg","projects/burlington/images/Manchu Bride in her wedding clothers.jpg","projects/burlington/images/Manchu Bride, Peking, Penchilie province.jpg","projects/burlington/images/Manchu Ladies at Meal Table.jpg","projects/burlington/images/Mao Changxi.jpg","projects/burlington/images/Pailou at Dagao hall, Beijing.jpg","projects/burlington/images/Percival David, Exhibition Director_ Zheng Tianxin (Dr F.T. Cheng), Special Commissioner of the Chinese Government and Zhuang Shangyan (Chuang Shang-Yen) and Tang Xifen (Tang Hsi Fen), Secretaries accompanying objects..jpg","projects/burlington/images/Praya or des Voeux road, Hong Kong.jpg","projects/burlington/images/Siamese Buddhist Bonze.jpg","projects/burlington/images/Siamese Dancing Girls Lakhon.jpg","projects/burlington/images/Sir Percival David, Exhibition Committee Director and Tang Xifen (Tang Hsi Fen) Exhibition Secretary, examining exhibits.jpg","projects/burlington/images/Sir Walter Lamb and Zhuang Shangyan (Chuang Shang-yen) checking cases as they arrive at Burlington House.jpg","projects/burlington/images/The Amitabha Buddha in the Central Hall- view from the Vestibule .jpg","projects/burlington/images/The Amitabha Buddha in the Central Hall.jpg","projects/burlington/images/Transfering cases from H.M.S. Suffolk to H.M.S. Hood at PortsmouthTransfering cases from H.M.S. Suffolk to H.M.S. Hood at Portsmouth.jpg","projects/burlington/images/Unloading a case from H.M.S. Suffolk at Portsmouth.jpg","projects/burlington/images/Unloading exhibits at the West Yard of Burlington House.jpg","projects/burlington/images/Unpacking exhibits from Japan.jpg","projects/burlington/images/War elephant of Siam.jpg","projects/burlington/images/_Magic Lantern_ Peep Show.jpg","projects/burlington/images/a nightwatchman, Peking.jpg"]},"description":"How China used an art exhibition to garner sympathy and respect from western sociaties"},"markdown":"# The social effect of the 1935-6 exhibition of Chinese art at Burlington House, London\n\n## Introduction\n\nChina is seen today as one of the great powers shaping the global economy. It regularly appears in conversations about trade, globalisation, environmentalism, conflict, ...etc. This is a long way from the late 19th early 20th century China whose population was mainly farmers, living so close to the breadline that a drought between 1876 and 1879 caused over 10 million deaths. The various uprisings towards the end of the Qing dynasty left the country split into factions, run by militarists and warlords who exploited the lands and people in endless conflicts for supremacy. This isnt even accounting for all the imperialist aims of the west and Japan who continued through brutish force to take territories and intervene in Chinas politics. (Bickers, 2017, p.0-76)\n\nI chose to write about China and its struggle for a place on the Global stage of economic powers when searching for something to write about around art activism. This is a story which rarely comes up in this context because it isnt traditional activist art which the Tate defines as a term used to describe art that is grounded in the act of doing and addresses political or social issues (Tate, 2017). I am using this definition loosely when calling the 1935 exhibition of Chinese art a form art activism, because the art itself, was not created with the purpose of instructing social or political change, but the choice to exhibit the art was driven in part by politics. What is central to this essay are the attitudes of those in the West (Europeans, Americans) towards the Chinese before and after the exhibition. \n\nI start this essay by looking at key historical events in China at the turn of the 20th century and how that shaped the opinions the Chinese had of their rulers. I also look at the prevailing opinions in the West of the Chinese. Then I look at the exhibition, how and why it came about and the political and social goals that it came with. Finally, I try to understand what effect if any the exhibition might have had by looking at the change in attitudes of those in the West.\n\nFor much of the historical context, I use Robert Bickers book Out of China published by penguin in 2017, which goes over the rise of China in great detail. Bickers is a professor of History at the University of Bristol and has published eight other books relating to modern Chinese History. \n\n## Early 20th century China\n\nIn 1894 the Chinese suffer a defeat at the hands of the Japanese losing Taiwan, in 1897 Germany take Jiaozhou Bay, in 1908 Korea was detached from China by the Japanese. The problem for China was not only external but endless uprisings were also weakening the state and the 1900 Boxer rebellion lead to further European concessions. This is the end of a long line of uprisings, defeats and humiliations suffered by the Chinese in the later part of the Qing dynasty. The Chinese felt disillusioned by the state, they had lost faith in its competence and worried about national extinction (wangguo). Nevertheless, when The Republic of China took over in 1912 it was with most territories still intact. (Bickers, 2017)\n\nThe view of China in Europe and America was that of degradation or as the President of the American Sociological Association wrote, China is the European middle ages made visible (Ross, 1911). Along with essays and books published by the Macartneys mission, the view in the west was that Chinas government took the form rational, enlightened despotism (Bickers, p.143). During the Nationalist revolution in the 1920s china saw an influx of foreign journalists, although there were many balanced accounts it was the critics such as J.O.P Bland and Rodney Gilbert whos writing most influenced foreign discourse (Bickers, 2017). Gilberts, Whats Wrong with China published in 1926 reflected the popular, at the time, scientific idea of social Darwinism and talked about breeds of men having a cultural limit which if succeeded would throw them into confusion and misery as the savage who is taken from his native wilderness (Gilbert, 1926, p.14). Despite admitting that he himself could not read Chinese literature, on the same page writes Chinese canonical philosophy is stuffy and ponderous, revealing at every turn a childish ignorance of first principles (Gilbert, 1926, p.44). With Gilberts 17 years experience in China as a journalist, his writings were taken seriously especially by those who mattered, business and military men (Bickers, 2017). There were others who wrote about the Chinese such as Arthur Waleys translations of Chinese and Japanese poetry which although widely read did not spread to those actually interacting with China (Bickers, 2017). \n\n## Lead up\n\nDespite the xenophobic views towards China so prevalent at the time, there were still small groups of collectors, academics and sinophiles who created groups like Vereniging van Vrienden der Aziatische Kunst (Society of Friends of Asiatic Art) in Holland and Freunde Ostasiatischer Kunst (Friends of East Asian Art) in Germany. The former coordinated one of the earliest exhibitions of Asian art in Amsterdam in 1925 pooling 212 pieces from private collections including the British collectors Sir Percival David and George Eumorfopoulos. A year later Freunde Ostasiatischer Kunst coordinated one in Cologne which was the first exhibition after the first world war to succeed in borrowing work from foreign European institutions. In 1929 an exhibition in Berlin was organised boasting 1,272 exhibits on loan from 171 public and private collections including three vases on loan from China. The exhibition had an accompanying lecture series and 458 page illustrated catalogue. The rapid growth from the 1925 Amsterdam exhibition to the 1929 Berlin exhibition suggests a fast increasing interest in Asian and specifically Chinese art and archaeology (Steuber, 2006). \n\nIn 1932 a group lead by Sir Percival David and George Eumorfopoulos proposed a comprehensive exhibition of Chinese art. After lengthy negotiations in 1934, the Chinese government decided to participate by lending large a quantity of work. Japan was pressing on Chinese territory after its occupation of Manchuria in 1931 and the Chinese government hoped that a display of its grandeur would garner sympathy and support from the west against Japanese imperialism (Steuber, 2006). The exhibition was also part of a series of nationally themed exhibits at the Royal Academy the most popular being the Italian with received over half a million visitors. With a strong endorsement from Chiang Kai-shek, many within the Chinese government saw this as a chance to establish its place on the global stage of cultures next to that of Persia and Italy (Steuber, 2006). \n\nThe exhibition wasnt without its controversy many in shanghai criticised Sir Percival Davids involvement who had lead groups in the Dunhuang expeditions in 1908 (Bickers, 2017). The Dunhuang expeditions lead to thousands of ancient relics being taken overseas and by now we're seen as theft of national treasures (Liu, 2016). These critics were further enraged by Eumorfopoulos and David who had asserted they and not Chinese experts knew Chinese culture best. With so much of the artwork being so intimately intertwined with Chinas imperial and national struggles, it must have taken some discerning and pragmatic calculation by Chinese officials to support the exhibition.\n\n## Exhibition\n\nOn the 28th of November 1935, the exhibition of Chinese art opened at the Burlington house London. With 780 pieces on loan from the Chinese government, the whole exhibition boasted a 3,080 piece collection (Bickers, 2017). Along with a lecture series which included prominent sinophiles and academics from as far away as the States this was the grandest display of Chinese heritage and culture to date. It received 401,768 paying visitors much more than the Persian exhibition with 255,724 visitors, or the French exhibition with 298,201 visitors, it was surpassed only by the Italian exhibition (Scaglia, 2016). The Times wrote when comparing it to western art that it is a saner and serener vision in which there was no room for our small greed and self-aggrandisement, and in which we realised our true place in the infinitely (The Times, 1935). The Manchester Guardian reported Chinese culture has at all times found a perfect means of expression in Chinese art (Newton, 1935).\n\nDespite its grandeur, earlier prejudices were still apparent especially within the mainstream media, the article titled Decorative objects published in The Times, 17 December 1935; showed an unwillingness to treat the work as serious art by using the word decorative (Liya, 2011). Chinese critics were often infuriated by Western attitudes who would refer to Oriental work as curios. Wen Yan-ning writes in an issue of T'ien Hsia, an English language periodical published first in shanghai, that the word curios, indicates a lazy and frivolous mind which prefers darkness to light (Liya, 2012). Sensibilities were changing though and art columnist Frank Davis criticised the British publics prejudices when he wrote Two sections alone should be sufficient to convince the average visitor that he is confronted with an art which can rise to monumental grandeur and still contain within itself every possible civilised grace (Liya, 2011). Lin Yutang who published a number of articles discussing Chinese art wrote in a piece called Artistic imperialism that even if the exhibition hadnt deepened intellectual understanding it had at least strengthening the sentimental bond between the Chinese and the British (Liya, 2012). \n\n## Aftermath\n\nThe exhibition lead to a boom in writing and translation of studies about Chinese art, literature and history, both in English and in other Western languages (Liya, 2012). Along with the writings and translations, studies of Chinese civilisation became much more popular among the authors were the poet Laurence Binyon and sinologist Arthur Waley. Both were successful in Britain but also received wide praise from the Chinese critics for their work translating Chinese poetry and literature. Wen Yan-ning editor of T'ien Hsia Monthly, wrote in a 1935 publication, Waley is no mere competent adapter, but a poet in his own right (Liya, 2012). The Burlington house exhibition certainly helped to catalyse some of these changes however it was not the only effort made by the Chinese to change foreign discourse. The Good Earth by Pearl S. Buck, published in 1931, a bestseller throughout the United States, dramatised rural family life in China (Isaacs, 1958). It became a Broadway show in 1932 and when a film was proposed by MGM China saw it as a chance to show a more favourable image of the Chinese than the gambling opium smokers that they so often appeared as in Hollywood (Bickers, 2017). With the threat of the Chinese government banning the movie and MGM losing the whole Chinese audience, they signed a deal with the Chinese government which stated that there always be a Chinese supervisor present and that every shot be reviewed by Chinese censorship. The movie was released in 1937 and by 1955 reached an estimated 23 million Americans. With over 2,000,000 books printed and the movie reaching an estimated 42 million people worldwide (Bickers, 2017). Harold Robert Isaacs in Scratches on our Minds writes that Pearl S. Buck, for a whole generation of Americans she created the Chinese\" (Isaacs, 1958, p.155).\n\n## Conclusion\n\nBetween 1920 and 1940 there was a large shift in foreign attitudes towards China and the Chinese. It is hard to say what was most influenced these changes nevertheless the 1935 exhibition was a defining moment, especially in the UK. In my research I didnt come across any sort of comprehensive analyses of direct effects and so am left with questions around the relative effect the exhibition actually had. When comparing the movie The Good Earth which had an overall audience of 42 million and the exhibition which had barely half a million visitors, it would seem that the movie must have had the bigger influence. But it might also be valuable to take into account the viewer demographics, it is likely especially for the time that the 1935 exhibition would have attracted a wealthy aristocratic type, the sort of person that might have had more influence on politics.This is of course speculation and it is just as easy to argue that it is the public's vote which most influences political course in which case the movie might have had a far greater effect. \n\nThe early 20th century China had a problem, they werent being taken seriously abroad and as a result were not being supported in any of their territorial disputes. But by carefully pushing an image of China that was both grand but also humble, not by pushing their own propaganda but by moderating Foreign cinema and promoting their art they were able to paint a new picture of themselves. Of course, this new image of China, of noble peasants, of a people who were wise, spiritual, deeply cultured and artistic, adept with brush and ink, and infused with a love of connoisseurship, was as much a caricature in its way and as unrepresentative as any other (Bickers, 2017, p.172).\n\n#### References\n\nBBC News. (2017). China profile - Timeline - BBC News. [online] Available at: [http://www.bbc.co.uk/news/world-asia-pacific-13017882](http://www.bbc.co.uk/news/world-asia-pacific-13017882)[Accessed 12 Mar. 2017].\n\nBickers, R. (2017). Out of China: How the Chinese ended the era of western domination. 1st ed. Allen Lane.\n\nChang-meng, Y. (2017). Recent Books on China. [online] Available at: [http://www.chinaheritagequarterly.org/030/features/pdf/1936/Articles/Recent](http://www.chinaheritagequarterly.org/030/features/pdf/1936/Articles/Recent) English Books on China.pdf [Accessed 15 Apr. 2017].\n\nGilbert, R. (1926). What's wrong with China. 1st ed. London.\n\nIsaacs, H. (1958). Scratches on our minds. 1st ed. M.E. Sharpe, pp.155-8.\n\nLiu, Z. (2016). The case for repatriating China's cultural objects. 1st ed. Springer, pp.13-16.\n\nLiya, F. (2011). Modern Chinese Intellectuals and the 1935 London International Exhibition of Chinese Art: The Reactions of Cai Yuanpei and The China Critic Group. [online] Available at: [http://publications.nichibun.ac.jp/region/d/NSH/series/kosh/2011-03-31/s001/s019/pdf/article.pdf]( http://publications.nichibun.ac.jp/region/d/NSH/series/kosh/2011-03-31/s001/s019/pdf/article.pdf) [Accessed 16 Apr. 2017].\n\nLiya, F. (2012). The 1935 London International Exhibition of Chinese Art: The China Critic Reacts. China Heritage Quarterly, [online] (30/31). Available at:[ http://www.chinaheritagequarterly.org/features.php?searchterm=030_fan.inc&issue=030]( http://www.chinaheritagequarterly.org/features.php?searchterm=030_fan.inc&issue=030) [Accessed 27 Mar. 2017].\n\nNewton, E. (1935). Chinese art: The Burlington House Show, First Impressions, Sculpture in Bronze, Jade and Pottery. The Manchester Guardian, p.9.\n\nRoss, E. (1911). Sociological Observations in Inner China. American Journal of Sociology, 16(6), pp.721-733.\n\nScaglia, I. (2016). The Aesthetics of Internationalism: Culture and Politics on Display at the 19351936 International Exhibition of Chinese Art. Journal of World History, 26(1), pp.105-137.\n\nSteuber, J. (2006). The exhibition of Chinese art at Burlington House, London, 1935-36. The Burlington Magazine, [online] pp.528-536. Available at: [http://burlington.org.uk/media/_file/generic/theexhibitionofchineseartatburlingtonhouse.pdf](http://burlington.org.uk/media/_file/generic/theexhibitionofchineseartatburlingtonhouse.pdf) [Accessed 8 Mar. 2017].\n\nTate. (2017). Activist art  Art Term | Tate. [online] Available at: [http://www.tate.org.uk/art/art-terms/a/activist-art](http://www.tate.org.uk/art/art-terms/a/activist-art) [Accessed 14 Apr. 2017].\n\nThe Times, (1935). Chinese art complementary to European a revelation to Britain. p.19.\n\nZheng, D. (1983). Studies in Chinese art. 1st ed. Hong Kong: Chinese University Press.\n\nImage References\nExhibition Catalogue: 28 November 1935 to 7 March 1936. (1935). 1st ed. London: Royal Academy of Arts.\n\nRoyal Academy of Arts Collections (2017). Buddhist Sculpture in The Lecture Room (Royal Academy of Arts, International Exhibition of Chinese Art, 1935-36). [image] Available at: [https://tinyurl.com/kpw5jvr](https://tinyurl.com/kpw5jvr) [Accessed 15 May 2017].\n\nThomson, J. (1869). Canton night watchman. Peking city guard. [image] Available at: [https://tinyurl.com/m4ljbg2](https://tinyurl.com/m4ljbg2) [Accessed 15 May 2017].\n\nThomson, J. (1871). Pailou at Dagao Hall, Beijing, China. [image] Available at: [https://wellcomeimages.org/indexplus/image/L0056443.html](https://wellcomeimages.org/indexplus/image/L0056443.html) [Accessed 15 May 2017].\n\nTopical Press Agency (1935). Sir Percival David, Exhibition Committee Director and Tang Xifen (Tang Hsi Fen) Exhibition Secretary, examining exhibits. [image] Available at: [https://tinyurl.com/m5lo2e7](https://tinyurl.com/m5lo2e7) [Accessed 15 May 2017].\n\n\n\n\n\n"},{"title":"Sustainable Design","meta":{"date":"02/03/2017","images":{"thumbnail":{"src":"https://images.unsplash.com/12/car.jpg?dpr=1&auto=format&fit=crop&w=767&h=463&q=80&cs=tinysrgb&crop="}},"description":"Heaps of manure lined the streets leaching into basements breeding flies and spreading disease."},"markdown":"# Sustainable Design\n\nAt the turn of the 20th century, New York had over 200,000 horses, 1 for every 17 residents resulting in 2000 tonnes of manure being dumped on the streets each day. Heaps of manure lined the streets, leaching into basements, breeding flies and worst of all spreading disease. The world had seemingly reached the point where its largest cities could not survive without the horse but couldnt survive with it, either (Levitt and Dubner, 2009, p.39). What saved us then was not a social move to cut down on the use of horses but the invention of the far cleaner, more efficient, cheaper, automobile.\n\nSustainable development generally refers to three areas: environmental stewardship, social equity and justice, and economic issues (Walker, 2006, p.16). The invention and adoption of the car at the beginning of the 20th century was a clearly a more sustainable development, however, this is not how it is seen today. Again we have reached a point where are cities are so congested and polluted, we are causing serious damage to our healths and the environment. A big question facing us now is whether we are going to have to dramatically change the way we live or whether again we can be saved by science.\n\nJust a couple of months ago a study was published in the journal nature communication that found photovoltaic solar will pay back, sometime this year, all the energy that it consumed over the last 40 years of development (Louwen et al, 2016). This doesnt mean much for us now but does mean essentially free energy for the future. Most designers arent also engineers and so arent at the forefront scientific discovery but they can push for social change and maybe, if enough artists and designers effectively educate people on the problems and consequences, we can tackle this crisis from both the technological and social sides.\n\n#### References\n\nCharlotte, K. (no date) Origins of Sustainability. Available at: https://tinyurl.com/lrohgz4 (Accessed: 2 March 2017).\n\nLevitt, S.D. and Dubner, S.J. (2009) Superfreakonomics: Global cooling, patriotic prostitutes and why suicide bombers should buy life insurance. London: Penguin Books.\n\nLouwen, A., van Sark, W.G.J.H.M., Faaij, A.P.C. and Schropp, R.E.I. (2016) Re-assessment of net energy production and greenhouse gas emissions avoidance after 40 years of photovoltaics development, Nature Communications, 7, p. 13728. doi: 10.1038/ncomms13728.\nWalker, S. (2006). Sustainable by Design: explorations in theory and practice. 1st ed. London: Earthscan, p.16.\n\n\n\n\n\n"},{"title":"Dissecting paintings","meta":{"date":"23/02/2017","images":{"thumbnail":{"src":"https://lh5.googleusercontent.com/uDb2BDJQW5-b6fAHOQSjFeQmFxyDR0BiIojKNz_zk_eyzjdRIuoQF0hW9czp1u8nw7aq7MsYK5yBD2YkWNh1tJ76gRghcaWipoX28bM75Me2gr9oX7x7-II3Cx6zGQP8qqmOIg8q"}},"description":"During a dark time for the israelites in their ongoing battle with the philistines Samson was born and god bestowed upon him supernatural strength with one caveat, he must not cut his hair","photosInline":[true,true]},"markdown":"# Dissecting paintings\n\nDuring a dark time for the israelites in their ongoing battle with the philistines Samson was born and god bestowed upon him supernatural strength with one caveat, he must not cut his hair. Samson become a hero to the israelites successfully defeating many philistines but one  day he fell in love with a woman called Delilah. Delilah was approached by the philistine rulers and offered silver in return for discovering how Samson could be defeated. Samson eventually tells her that the secret lay in his hair and that if it were cut his strength would leave him. (Judges 16: 17-20)\n\nThis story from the old testament is the story behind Peter Paul Rubens painting Samson and Delilah. In the painting Samson is asleep on Delilahs lap, while an old procuress holds a candle for the barber cutting Samsons hair. Above in the background is a sculpture of venus and cupid who are both gods of love and desire. To the right are the philistines lying in wait behind a door. The scene is well lit, in part at least by the fire on the left which lies half out of the painting.![401455.jpg](https://lh5.googleusercontent.com/uDb2BDJQW5-b6fAHOQSjFeQmFxyDR0BiIojKNz_zk_eyzjdRIuoQF0hW9czp1u8nw7aq7MsYK5yBD2YkWNh1tJ76gRghcaWipoX28bM75Me2gr9oX7x7-II3Cx6zGQP8qqmOIg8q)\n\nSome notable symbols of the painting are the old face of the procuress juxtaposed with the youthful complexion of Delilah which could allude to the procuresss past or Delilahs future (Samson and Delilah 2016). The statue of venus and cupid are a clear reference the cause of Samson's Fate (Samson and Delilah, 2009).\n\nPart of the goal of the exhibition visit was to look at these paintings through John Berger's eyes focusing on the representation of woman. The nakedness and position of Delila in Rubens painting reminded me of a painting by Sir Peter Lely in which John Berger describes the nakedness as not her own expression but is a sign of her submission to the owners feelings or demands (Berger, 1973, p.52). Here Berger refers to both the painter and the client who is paying for the painting who in both Lelys and Rubens pieces are male. Although the story does include a temptress the question still lies whether this painting might look very different if it were painted for and by a woman.![img](https://lh4.googleusercontent.com/2ssBxtvLrzFhReio1YDk0VFbz4jVUeLr0H0P6MYxqT2GMJEV2fJfaGVBKhPC0zVtjIeYWkc2ixA3SL-ITLiDTKHCUb3vKkJE-oKz-Jel6keGStNpq6Rk0UJ-csTySZXloEgq0uvy)\n\n#### References\n\nBerger, J. (1973) Ways of seeing based on the BBC television series. London, Eng.: British Broadcasting Corporation [u.a.].\n\nBible gateway passage: Judges 16 - new international version (no date) Available at: https://tinyurl.com/mg6h7tu (Accessed: 23 February 2017).\n\nLely, S. (n.d.). Portrait of a young woman and child, as Venus and Cupid. [oil on canvas].\n\nRubens, P. (1609). Samson and Delilah. [Oil on wood] London: National Gallery.\n\nSamson and Delilah. (2009). In National Gallery, National gallery collection. [Online]. London: The National Gallery. Available from: https://tinyurl.com/mcrzcgk [Accessed 23 February 2017].\n\nSamson and Delilah. (2016). In E. Langmuir, The National Gallery companion guide. [Online]. London: The National Gallery. Available from: https://tinyurl.com/k689nch [Accessed 23 February 2017].\n\n\n\n"},{"title":"Walk Through British Art","meta":{"date":"08/02/2017","description":"Having an exhibition laid out in chronological order helped me to understand art and art styles in respect to history","images":{"thumbnail":{"src":"http://www.tate.org.uk/art/images/work/T/T02/T02270_10.jpg"}},"photosInline":[false]},"markdown":"# Walk Through British Art\n\nThe 'Walk Through British Art' exhibition comprised of British art ranging from 1540s right up until the 21st century. As the name suggests you are able to start at one end and walk through each room following the artwork in roughly chronological order. Seeing art laid  out this way, lead me to reflect on the evolution of art and the differences century to century. The starkest of these differences was between the 19th and 20th centuries where the art became much more abstract. There also seemed to be a correlation between date and quantity with half the exhibition taken up by the 20th-century artwork.\n\n![Reclining Figure](http://www.tate.org.uk/art/images/work/T/T02/T02270_10.jpg)\n\nThere was a whole room dedicated to Henry Moore's sculptures and situated on a plinth in the centre of this room is Reclining Figure. It depicts what looks like a woman, judging by the protuberance on her chest, lying propped up by both elbows. What made the piece stand out among the other sculptures was the contrast between the dark lines on the lighter plaster. They make the piece look as though it is made from separate chunks and gives it an almost space-age look. I discovered later that Moore himself singled it out as one of his most important sculptures saying that it was the first sculpture in which I succeeded in making form and space sculpturally inseparable (Christies, 2016).\n\n\nHaving an exhibition laid out in chronological order helped me to understand art and art styles with respect to history. There seemed to be an evolution in painting starting in two dimensions moving into perspective and three dimensions and finally to abstraction. It made me wonder what people a few hundred years ago would think of a sculpture like Reclining Figure, is abstraction attractive to only the modern sentiment or is it just a more recent discovery?\n\n#### References\n\nChristies,  (2016) My highlight of 2016  Henry Moores Reclining Figure: Festival. Review of by Robert Brown. Available at: https://tinyurl.com/k6d4yjw (Accessed: 9 February 2017).\n\nMoore, H. (1951). Reclining Figure. [Plaster and string] London: Tate.\n\nTate (1951) Reclining figure, Henry Moore OM, CH 1951 | Tate Available at: https://tinyurl.com/kn4h3n5 (Accessed: 16 February 2017).\n\n\n\n"},{"title":"What is Semiotics?","meta":{"date":"17/01/2017","description":"To decline the study of signs is to leave to others the control of the world of meaning which we inhabit."},"markdown":"# What is Semiotics?\n\nSemiotics is defined in the oxford English dictionary as The study of signs and symbols and their use or interpretation (semiotics, 2017). This is the first time I have come across semiotics so I use this essay to explore where it came from and what other subjects it is related to.\n\nOne of the earliest times a word relating to semiotics appeared was in Swiss linguist Ferdinand de Saussures A Course in general linguistics, A science that studies the life of signs within society is conceivable; it would be a part of social psychology and consequently of general psychology; I shall call it semiology (de Saussure, 1974,  p. 16). He goes on to define a sign by splitting it into two components, the signifier and signified. The signifier is the literal sign: sound, image, text and the signified is the concept or idea that the sign is referring to. Depending on your definition It may be misleading to call semiotics a science Daniel Chandler, in his book Semiotics: the basics explains that it has no widely agreed theoretical assumptions, models or empirical methodologies (Chandler, 2006, p. 4).\n\nSaussure does give a sort of framework to work from when he defines the sign as being made from the signified and the signifier. Using this definition of a sign it is possible to break down and compare different signs and debate their connotations, denotations with some level of logical progression.\n\nI found it helpful to see linguistics as a subset of semiotics, it being the study of perhaps our most valued sign system language. It is interesting to step back and look at language like this, comparing it to our other sign systems such as mathematical notation, music, or body language. Looking from this perspective it is easier understand what Chandler means when he says To decline the study of signs is to leave to others the control of the world of meaning which we inhabit. (2006, p. 11).\n\n#### References\n\nChandler, D. (2006) Semiotics: The basics, Vol. 2. 2nd edn. LONDON: Taylor & Francis.\n\nde Saussure, F. (1974) Course in general linguistics. Edited by Charles Bally, Albert Sechehaye, and Albert Reidlinger. Translated by Wade Baskin. 2nd edn. New York: McGraw-Hill Book Company.\n\nPeirce, C.S.S., Hartshorne, C. and Weiss, P. (1932) Collected papers of Charles Sanders Peirce, volumes I and II, principles of philosophy and elements of logic. 4th edn. Cambridge, MA: Belknap Press of Harvard University Press, 1935-1961.\n\nsemiotics. (2017). In: Oxford, 1st ed. [online] Available at: https://en.oxforddictionaries.com/definition/semiotics [Accessed 15 May 2017].\n\n\n\n\n\n"},{"title":"Measuring the impact of culture on the economy","meta":{"date":"05/12/2016","description":"Each decision to fund one cause is also the decision not to fund another."},"markdown":"# Measuring the impact of culture on the economy\n\n## Introduction\n\nEconomic value in the Cambridge English dictionary is defined as the value of an asset calculated according to its ability to produce income in the future (Cambridge Dictionary 2016). This definition when applied to culture and the arts may not fit so well since much of the value we find in culture is not monetary. Neoclassical economics has a broader definition, it defines value as the rational preference of one outcome over another (Weintraub, 1993). This definition allows us to look more broadly at value while staying within the economic confines of rational thought. In this essay, I start by defining what I am referring to when I use the term the culture. Using the neoclassical definition of value, I try and pick apart what cultural commodities are and how they are different to other goods and services. I look at ways of measuring this value and the problems that arise as a result. Finally, I look at why measuring the value of culture matters at all.\n\n## What is culture\n\nThe word culture is hard to define and hotly contested though most people do having an intuitive sense of its meaning. Raymond Williams who has written much on the subject of culture defines first the independent noun culture, as indicating particular way of life, whether of a people, a period, a group, or humanity in general. Williams defines the abstract noun culture as describing the works and practices of intellectual and especially artistic activity (Williams, 1983, p.87). These definitions allow us to talk about culture in reference to a way of life and talk about art as a cultural commodity.\n\n## How to measure economic value of culture\n\nThere are three main methods economists use to measure the value of industries, sectors or goods, these include economic impact assessment, measuring size or footprint, cultural satellite accounts. Impact assessment can be split into three subcategories, direct impacts, indirect impacts and induced impacts. Direct impacts are the gross value of the goods and services (GVA) and the cost of employment. Indirect impacts are the economic impacts through the supply chain. Induced impacts are the effects of the spending by those employed within the industries. Size or footprint analysis is used to understand whether a sector is significant to the economy however, it doesnt include indirect effects and so the information it gives is limited. Cultural satellite accounts look at many cultural industries accounts in including the accounts of the suppliers and distributors in order to evaluate the national contribution. This is often used when measuring the size of sectors that are not industries such as tourism or unpaid housework. (Crossick and Kaszynska, 2016, p89-90)\n\nThe most popular of the three methods for calculating the value of cultural commodities are economic impact studies, the one that resonates most with elected leaders, public and private sector funders, and policymakersis the economic impact study (Sterngold, 2004, p167), however there is a body of work criticizing this approach to measuring the value of cultural sectors. Sterngold notes that problems arise when substitution effects are not taken into account. For example, you might want to measure the potential economic impact of a new cinema, it is not enough to just measure the gross income generated because a proportion of that income will be income originally generated by another business. In other words by funding a project like a cinema you might just be shifting people's spending from one business to another and not actually generating new wealth for the regen (Sterngold, 2004). It is also important to note that impact studies are not an exact science but a best estimate if multiple impact studies are done they will in all probability yield different results (Crompton, Lee, and Shuster, 2001). Because of this, it is important to be critical of impact studies done and especially any vested interest of authors or funding bodies.\n\nBroader problems arise from using an economic approach to measuring the value of cultural commodities in general, since classical economics treats people as purely rational decision makers, in other words it assumes individuals will take a logical approach to maximising economic profit. This does not seem to be the case with artistic labour where financial rewards are generally lower and there is a  high variability of artists earnings (Ginsburgh, 2006, p9). Ginsburgh also notes the there are  non-pecuniary motives where artists care more about their work than financial incentives (Ginsburgh, 2006, p9).\n\nIt is important to also mention social return on investment (SROI) which is an alternative method used to calculate the social value of cultural projects, that in recent years has gained popularity. The method produces a ratio which is supposed to reflect the ratio of monetary investment value, to social return value. A key aspect of the SROI method is that it stresses the importance of a detailed report containing qualitative, quantitative and financial data that explains the judgements and decisions made over the course of the analysis (Jardine and Whyte, 2013, p22). A detailed qualitative and quantitative report would help to eliminate the problem that impact assessments suffer from which is that they are hard for non economists to fully understand and so be critical of.\n\n## Why the measure economic value of cultural commodities\n\nAll government funding decisions are subject to opportunity cost (Crossick, p90, 2016), meaning each decision to fund one cause is also the decision not to fund another. If policy makers and funding bodies can see not only public support but imperative evidence that funds in a cultural project will generate jobs and wealth for the country/regen it will make decisions between say in investment in a science project or cultural project much clearer. There are also the voluntary/third sector and charities who are under increasing pressure to evidence their effectiveness (Jardine and Whyte, 2013, p1).\n\nIn a study completed in 2013 on the value of the British Library, Andrew Tessler uses benefit-cost analysis (BCA) to calculate a benefit-cost ratio (BCR). This is done by weighing up all the benefits and dividing by all the costs. To estimate the value of the the library Tessler uses Total Economic Valuation (TEV) which takes account of both use-values and non-use values. The use values are all the benefits felt by users of the library such as the reading-rooms, document supply, web-services etc. The non-use values are existence value which is The value attached to the existence of the Library irrespective of whether a person ever visits it or not., bequest value,  The value which the current generation places on preserving the Library for the benefit of future generations and option value, This represents the value attached to potential future benefits from the Library.. To work out the values of the amenities offered by the library they surveyed users asking how much the users were willing to pay (WTP) for their usage. For non-use values surveys were sent out to nation wide as to ensure that the respondents reflected the UK population distribution. 55% of the surveys received back were from people who had never heard of or used the library and by asking If you were free to choose how your existing taxes were allocated, what would be the maximum you would be willing to pay, the average amount received back was 8.15 which is over double what is currently being paid. The study concluded that The benefit cost ratio of 4.9 indicates that for every 1 invested in the Library in 2011/12, the Library generated 4.90 in economic welfare. (Oxford Economics, 2013)\n\nIt might be argued that the study above is not all that useful since it seems unlikely that the government would ever cut funding to something as culturally significant as the British library. Nevertheless, the study does demonstrate that there is a significant both social and economic return on an investment which might not be immediately obvious.\n\n## Conclusion        \n\nIt seems clear that if possible the ability to define the true economic value of goods and services within the cultural sector could not only benefit the public but also those making the funding decisions. Public policy and public funding are a crucial part of the overall landscape occupied by arts and culture (Crossick, p154, 2016). The real difficulties arise when knowing whether the numbers you have come up with truly reflect the actual value. In this essay, I have addressed the cultural sector as a whole rather than looking at its constituent parts. This might be the wrong way of addressing the question how to measure economic value?, it might be better to focus on sectors of culture such as the music, museums, public art, architecture etc. There are of course other ways to measure the value of culture, one of the most promising being (SROI), which might do a much better job of reflecting the true value culture has to people and society. It seems there is still too little evidence to say which method is the most accurate and it is likely that the best method will be specific to the situation its used in.\n\n#### References\n\nCambridge Dictionary (2016) Economic value meaning in the Cambridge English dictionary. Available at: [http://dictionary.cambridge.org/dictionary/english/economic-value?fallbackFrom=learner-english&q=Economic+value](https://www.google.com/url?q=http://dictionary.cambridge.org/dictionary/english/economic-value?fallbackFrom%3Dlearner-english%26q%3DEconomic%2Bvalue&sa=D&ust=1517590256640000&usg=AFQjCNGEZ0uIZwiZxygYK5W2CQGn13TeKw) (Accessed: 5 December 2016).\n\nCrompton, John L., Seokho Lee, and Thomas J. Shuster. 2001. A guide for undertaking economic impact studies: The Springfest example. Journal of Travel Research 40:7987.\n\nCrossick, G. and Kaszynska, P. (2016) Understanding the value of arts & culture, The AHRC Cultural Value Project\n\nE. Roy Weintraub, (1993). \"Neoclassical Economics.\" The Concise Encyclopedia of Economics. Library of Economics and Liberty. Retrieved from [http://www.econlib.org/library/Enc1/NeoclassicalEconomics.html](https://www.google.com/url?q=http://www.econlib.org/library/Enc1/NeoclassicalEconomics.html&sa=D&ust=1517590256641000&usg=AFQjCNFiOFkPuH4siyS0iiqHs1q9wMtrxQ)\n\nGeursen, G. and Rentschler, R. (2003) Unraveling Cultural Value, The Journal of Arts Management, Law, and Society, 33(3), 196-210. doi: 10.1080/10632920309597346\n\nGinsburgh, V. and Throsby, D. (2006) Handbook of the economics of art and culture. North Holland\n\nJardine, C. and Whyte, B. (2013), Valuing Desistence? A Social Return on Investment Case Study of a Throughcare Project for Short-Term Prisoners. Social and Environmental Accountability Journal, Vol. 33, No. 1, 2032, [http://dx.doi.org/10.1080/0969160X.2013.768088](https://www.google.com/url?q=http://dx.doi.org/10.1080/0969160X.2013.768088&sa=D&ust=1517590256643000&usg=AFQjCNE9zz5iDbYI-hlqNz5Rz8Np_ZwUMA)\n\nNelson, P. (1970). 'Information and Consumer Behavior, Journal of Political Economy, Vol. 78, No. 2 (Mar. - Apr., 1970), pp. 311-329 [http://www.jstor.org/stable/1830691](https://www.google.com/url?q=http://www.jstor.org/stable/1830691&sa=D&ust=1517590256643000&usg=AFQjCNGtShto7f4LEginhqQEsbbBgMaPZQ)\n\nOBrien, D. (2010). Measuring the Value of Culture: a report to the Department for Culture Media and Sport. London: DCMS. Retrieved from https://www.gov.uk/government/uploads/ system/uploads/attachment_data/file/77933/measuring-thevalue-culture-report.pdf\n\nOxford Economics. (2013). Economic Valuation of the British Library. Oxford: Oxford Economics. Retrieved from [www.oxfordeconomics.com/my-oxford/projects/245662](https://www.google.com/url?q=http://www.oxfordeconomics.com/my-oxford/projects/245662&sa=D&ust=1517590256644000&usg=AFQjCNGWhHws8yD0htb00jzkBqCWvu6Xhw)\n\nSterngold, A.H. (2004). Do economic impact studies misrepresent the benefits of arts and cultural organizations? Journal of Arts Management, Law, and Society, 34 (3), 166-187\n\nThrosby, D. (2003). Determining the Value of Cultural Goods: How Much (or How Little) Does Contingent Valuation Tell Us?, Journal of Cultural Economics. doi:10.1023/A:1026353905772\n\nWilliams, R. (1983). A vocabulary of culture and society, Oxford University Press. Retrieved from [https://tavaana.org/sites/default/files/raymond-williams-keywords.pdf](https://www.google.com/url?q=https://tavaana.org/sites/default/files/raymond-williams-keywords.pdf&sa=D&ust=1517590256645000&usg=AFQjCNGmV8vWY0ohoNUS6fUXq1iAnS2RnA)\n\n\n\n"},{"title":"Why interaction design?","meta":{"date":"17/11/2016","description":"Computers are only becoming more ubiquitous and they have been at an accelerated rate since the beginning of their conception","images":{"thumbnail":{"src":"https://lh3.googleusercontent.com/wWb8VtJnrY7Zv0ceGZNBYFhB0I5OXmRul89xrIR6cAo8E_PpOvk8hyR6nv0DCbVzqDOIUm_JHoQPupfbu5UM8GB8iHR8eVZoGVX51tPx0ZYGChzBekGyTTd9RelZU9C5myPuH9CR"}}},"markdown":"# Why interaction design?\n\nWith a focus on interaction design in this weeks lecture we discussed the various forms that design takes. We touched on experience design and immersive design two disciplines whose methods closely overlap those of interaction design. We also talked more broadly about the different disciplines that come under design which are neatly illustrated in Thomas Pluralvonglas (2009) venn diagram on the right. In this essay I explore further what interaction design means, what sort of problems it is there to solve and touch on what it means to my own work.![img](https://lh3.googleusercontent.com/wWb8VtJnrY7Zv0ceGZNBYFhB0I5OXmRul89xrIR6cAo8E_PpOvk8hyR6nv0DCbVzqDOIUm_JHoQPupfbu5UM8GB8iHR8eVZoGVX51tPx0ZYGChzBekGyTTd9RelZU9C5myPuH9CR)\n\nComputers are only becoming more ubiquitous and they have been at an accelerated rate since the beginning of their conception (Sade 2006). Due in part to the rate at which they are developing and also the fact that they tend not to be built by designers eminently focused on the end user but by engineers trying to solve technical problems. I still see many especially in the older generations who struggle to understand and use computers. Or atleast it is hard for them to see the benefits when the time and energy investment for them to learn these systems is so large. Interactions design questions usability it inspects our behaviour and analyzes the choices we make (Kolko, 2009). This is why I believe it can play an important role in not only making technology more accessible but question the way we use it.\n\nIn the studio I am very conscious of this idea of questioning the way we use and interact with objects and technology. With this in mind in response to my latest brief on visualizing time I am looking at new ways of interacting with a book. I am trying to use interactivity as a tool to teach technical abstract ideas.\n\n#### References\n\nKolko, D.J. (2009) Thoughts on interaction design. Morgan Kaufmann Publishers.\n\nSade, G. (2006) Why interaction design? Review of Jonas Lwgren and Erik Stolterman Thoughtful interaction Design, Design Philosophy Papers, 4(1), pp. 4152. doi: 10.2752/144871306x13966268131352.\n\nPluralvonglas, T. (2009) The Disciplines of User Experience Design. [Infographic].\nAvailable at [http://bit.ly/NZ3dxo](https://www.google.com/url?q=http://bit.ly/NZ3dxo&sa=D&ust=1517590438654000&usg=AFQjCNFsYlL_K-rtEDxX1XGHtoO7Wpp0rw). Accessed 20th October 2016\n\n\n\n\n\n"},{"title":"Research methodologies","meta":{"date":"10/11/2016","description":"A large part of the researcher's job is analysing their own input, being aware of their own role in what they are studying."},"markdown":"# Research methodologies\n\nIn todays lecture we were introduced to research methodologies more specifically qualitative research and quantitative research. We covered research strategies, ethics and the reasons one might want to perform a piece of research. In this essay I try to define in detail the difference between qualitative and quantitative research.\n\nQualitative research is especially helpful in situations where what you are studying is too complex to isolate a single variable such as people in social situations. Researchers often immerse themselves in the setting in order to see what they are studying in context (Mason, 2002). This means a large part of the researcher's job is analysing their own input, being aware of their own role in what they are studying. This is one of the criticisms of the qualitative approach since the researcher's presence has a profound effect on the subjects and thus experiments are not repeatable  (Hughes). Despite its shortcomings it plays an important role in studying complex systems it is the qualitative approach which often leads to new insights and opens avenues for further quantitative research (Hughes).\n\nQuantitative research is used when testing a single variables relationship to another. Researchers will start with a clearly defined question and design all the details of the study before any data is collected. The results of the research are structured tables of values and statistics. Computers allow researchers to process large data sets making it possible to make accurate generalizations and predictions. Quantitative research only works in systems where you can totally isolate variables and when this is the case it can be effective at understanding true objective relationships. (USB Libraries, 2016)\n\nThese are not totally separate techniques and when researching outside of science it is often effective to use both qualitative and quantitative methods (Denscombe 2010). For my studio work though I can see the qualitative method as being more useful as so much of my work involves many unknowns and tends to be exploratory.\n\n#### References\n\nDenscombe, Martyn. (2010) Good Research Guide: For small-scale social research projects. 4th edn. Berkshire: Open University Press\n\nHughes, Christina, An Introduction to Qualitative Research. Department of Sociology. University of Warwick. Available at [http://www2.warwick.ac.uk/fac/soc/sociology/staff/hughes/researchprocess/](https://www.google.com/url?q=http://www2.warwick.ac.uk/fac/soc/sociology/staff/hughes/researchprocess/&sa=D&ust=1517590674249000&usg=AFQjCNFDdtVBD_hEKaZL-zfNiTiQWAWLRQ). (Accessed 29t November 2016).\n\nMason, J (2002) Qualitative Researching, 2nd edn. SAGE Publications Ltd\n\nWilson, V (1997) Focus Groups: A Useful Qualitative Method for Educational Research? British Educational Research Journal, 23, 2, pp 209-224\n\nUniversity of Southern California. (2016) Organizing Your Social Sciences Research Paper: Quantitative Methods. Available at http://libguides.usc.edu/writingguide/quantitative. (Accessed 8t November 2016).\n\n\n\n"},{"title":"The infinite mix exhibition review","meta":{"date":"01/11/2016","description":"Are there everyday activities, movements which deserve more attention?"},"markdown":"# The infinite mix exhibition review\n\nThe infinite mix exhibition was comprised of 10 audiovisual artworks by 10 leading international artists. Each piece played closely with video and sound and in this essay, I talk about two pieces which I found especially interesting.\n\nFirst when you walk in, is Martin Creeds work No. 1701 from 2013. It is a short movie shot entirely on a crossing in new York. With the camera on a dolly, it tracks people one at a time as they make there way across the road. Each of the people had a unique walk often suffering from an ailment. Without the jubilant rock song playing in the background it could have been almost pitiful. Instead, I felt captivated by the struggles that some endured. It was a chance to examine the unique walks of those that politeness might not normally allow us to do. I believe it is the elated music in the background which changed the tone of the piece from that of pity to that of stoicism.\n\nThe next piece which intrigued me was m.A.A.d 2014 by Kahlil Joseph. Like many of the pieces, it crossed somewhere in between a documentary and a music video. In part, it felt like a contemporary gangster rap music video with the way it flaunted tattoos gold teeth and guns. However the feeling I felt was more of respect for the harsh unforgiving environment that those growing up in Compton Los Angeles had to deal with.\n\nMartin Creeds piece got me thinking about whether there are other places or things that we are not normally free to inspect. Are there other everyday activities movements which deserve more attention. I hope I can find and develop this idea further in my studio work and maybe find a different way to highlight the everyday physical struggles that so many go through.\n\n#### References\n\n infinite mix (2016) Available at: http://www.theinfinitemix.com/ (Accessed: 5 December 2016).\n\n\n\n"},{"title":"Modernism and Scottish school","meta":{"date":"31/10/2016","images":{"thumbnail":{"src":"https://lh6.googleusercontent.com/71lc94XYmJR_fqO4_1oDA8nfe9XSawjUHpGHvaSWj5kY_u1c0ZuGR62F0ybI1zSDliWQVuSri8SabzzMMWtboLPAQQvXwTpflFzToJw8J5hN7wpf18wbOehBhaNcA1XMSrbJzhFx"}},"description":"Mackintoshs chair like Breuers uses simple geometric shapes, the main and key difference is in the choice of materials."},"markdown":"# Modernism and Scottish school\n\nIn this week's lecture we covered seven of the major art movements in the 20th Century, Scottish School, Art Nouveau, Modernism, Art Deco, Surrealism, Cold War Design and Postmodernism. In the essay I look closer at Scottish school and modernism using their most iconic chairs.\n\n## Scottish school\n\nScottish school was fashionable between 1885 and 1915, formed in glasgow and edinburgh it aimed to produce work which improved contemporary cosmopolitan life (Victoria, Museum, 2013b). One of the most prominent artists of the time was Charles Rennie Mackintosh whose chair is pictured on the right.\n\nScottish school is famous for geometric shapes and combining straight lines with curves (Victoria, Museum, 2013b). This can be seen in the back of this chair which has a rectangular footprint but is framed by two legs which start of square but elegantly taper to circular. The back also has an interesting symmetry between the oval head rest and the oval curve of the support at the bottom.![circ.130-1958_chair_1000px.jpg](https://lh4.googleusercontent.com/yDNP0ibmtWRx-lGt9Qio4yaxbF_QGMw4Q0xgMN-RzHHO6pfGHoCcASilWzLN14FIwa3owMzgHb-xg8rSMWGL-YdKwS8kp3ahNUAmVA37j5MY6tiOo_e8oa-Vn1wlYd0nOzDzLqcd)\n\n## Modernism\n\nModernism is a post second world war movement notable for the philosophical mindset that technology and design could change society. They believed in design for the masses and this is seen in the style, which is often minimal and geometric (Victoria, Museum, 2013a).\n\nWassily chair by Marcel Breuer was the obvious choice for c to construct a folding and none folding chair. The use of industrial materials and the relatively ma modernist chair. Inspired by the design of his motorbike Breuer used chrome plated steel tubes and minimal design mean it can be manufactured at scale (La Fonda del Sol 2012).\n\nMackintoshs chair like Breuers uses simple geometric shapes, the main and key difference is in the choice of materials. Mackintoshs chair would be much harder to produce at scale it also lacks in my eyes the aesthetic balance the Breuers has.![B3Wassily_Breuer_02.jpg](https://lh6.googleusercontent.com/71lc94XYmJR_fqO4_1oDA8nfe9XSawjUHpGHvaSWj5kY_u1c0ZuGR62F0ybI1zSDliWQVuSri8SabzzMMWtboLPAQQvXwTpflFzToJw8J5hN7wpf18wbOehBhaNcA1XMSrbJzhFx)\n\n#### References\n\nLa Fonda del Sol  exhibition tour & dinner / SPECIAL (DE) (2012) Available at: http://www.design-museum.de/en/collection/100-masterpieces/detailseiten/b3wassily-marcel-breuer.html (Accessed: 5 December 2016).\n\nVictoria, Museum, A. and webmaster, D.M. (2013a) Modernism - Victoria and Albert Museum. Available at: http://www.vam.ac.uk/page/m/modernism/ (Accessed: 5 December 2016).\n\nVictoria, Museum, A. and webmaster, D.M. (2013b) Style guide: Scottish school - Victoria and Albert Museum. Available at: http://www.vam.ac.uk/content/articles/s/style-guide-scottish-school/ (Accessed: 5 December 2016).\n\n\n\n\n\n"},{"title":"What is Culture?","meta":{"date":"31/10/2016","description":"The search for a definition of culture felt like traversing a tangled web of ambiguity leading from one historical piece of literature to the next."},"markdown":"# What is Culture?\n\nIt is important especially in academic writing to have clear definitions for words and terms used. This is not always easy, meanings are continually changing and many words have more than one meaning depending on the context. In this post I try to investigate the true definition and where the word came from.\n\nThe Oxford english dictionary defines culture as \"The arts and other manifestations of human intellectual achievement regarded collectively\" (Oxford English Dictionary, 2016). The Cambridge english dictionary  defines culture as \"the way of life, especially the general customs and beliefs, of a particular group of people at a particular time\" (Cambridge Dictionary, 2016).\n\nThe history of the word starts in medieval times and comes from the word cultivate when referring to the cultivation of land (Williams, 2016, p. 242). The latin french origins cultura and culture respectively also refer to farming land. The first sign of its meaning changing was Thomas Hobbes's Leviathan first published 1651 which uses culture to describe the education of children, the education of Children a Culture of their minds (Hobbes, 2008, p. 163). Raymond Williams (1953), describes William Wordsworth's use of the word in The Prelude (1995) as genuinely transitional; when he uses the word in both the older sense of process, be left to droop by timely culture unsustained, but also uses it as an absolute, Salutes the Being at his birth, where grace of culture hath been utterly unknown. This according to Raymond Williams was the first time culture was used in the modern sense of the word (Williams, 1953).\n\nThe search for a definition of culture felt like traversing a tangled web of ambiguity leading from one historical piece of literature to the next. There was no single definition which encapsulated all of the meanings but maybe that's okay, maybe the job of someone using the term is to clearly define the context they use it in.\n\n#### References\n\nCambridge Dictionary (2016) Culture meaning in the Cambridge Learners dictionary. Available at: http://dictionary.cambridge.org/dictionary/learner-english/culture (Accessed: 5 December 2016).\n\nHobbes, T. (2008) Leviathan (Oxford worlds classics). Edited by J. C. A. Gaskin. Oxford: Oxford University Press, USA.\n\nOxford English Dictionary (2016) Available at: https://en.oxforddictionaries.com/definition/culture (Accessed: 5 December 2016).\n\nWilliams, R. (1953) The Idea of Culture, The Cambridge Quarterly, 3(3).\n\nWordsworth, W. (1995) The prelude: The Four texts (1798, 1799, 1805, 1850): Four texts (1798, 1799, 1805, 1850). Edited by Jonathan Wordsworth. New York: Penguin Group (USA).\n\n\n\n"}]

